@import "./variables";

@mixin  text ($fF, $fS, $lH){
  font-family: $fF;
  color: $textColor;
  line-height: $lH;
  font-size: $fS;
  font-weight: 400;
  letter-spacing: 1px;
}
@mixin  block ($bC){
  background-color: $bC;
  display: flex;
  align-items: center;
}
@mixin  context ($jC){
  //border: 3px solid #0c357d;
  height: 100%;
  width:70%;
  margin: 100px auto;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: $jC;
  @media screen and (max-width: 710px) {
    width: 95%;
  }

  }

@mixin itemsContainer(){
  margin-top: 35px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
}